import {
    Box, Button, Accordion,
    AccordionItem,
    AccordionButton,
    AccordionPanel,
    Image
} from "@chakra-ui/react"
import "./MenuStyles.css"
import { CloseIcon, HamburgerIcon } from "@chakra-ui/icons"

export default function Menubutton() {


    return (
        <Accordion allowToggle>
            <AccordionItem>
                {({isExpanded}) => (
                    <>
                    <Box  display={'flex'} justifyContent='space-between' alignItems={'center'}>
                    <Box>
                     <Image src={'https://i.ibb.co/DLBf6yv/name.png'} w={"80%"} h='80' />
                    </Box>
                    <Box>
                    <AccordionButton backgroundColor={'transparent'} border={'none'}>
                        <Button backgroundColor={'transparent'} border={'none'} colorScheme="black" leftIcon={isExpanded ? <CloseIcon fontSize={'1.5rem'} /> : <HamburgerIcon fontSize={'1.5rem'} />} />
                    </AccordionButton>
                    </Box>
                </Box>
                
                <AccordionPanel h='100%'>
                    <Box w='100%' h='100%' display={'flex'} flexDir="column" justifyContent={'center'} alignItems='center' textTransform={'uppercase'}>
                            <ul className="menu-list">
                                <li>Home</li>
                                <li>About Me</li>
                                <li>Skills</li>
                                <li>Project</li>
                                <li>Contact</li>
                                <li>Resume</li>
                            </ul>
                    </Box>
                </AccordionPanel>
                </>
                )}    
            </AccordionItem>
        </Accordion>
    )
}